/*
 * Desktop
 */
@media only screen and (min-width: 1366px) and (max-width: 1600px) {
  .sectionList .sectionList__section:nth-last-child(-n + 3) {
    margin-bottom: 0; } }
/*
 * Desktop
 */
@media only screen and (min-width: 816px) and (max-width: 1365px) {
  .sectionList .sectionList__section:nth-last-child(-n + 3) {
    margin-bottom: 0; } }
/*
 * Desktop
 */
@media only screen and (max-width: 815px) {
  .sectionList {
    -ms-flex-pack: center;
        justify-content: center; }
    .sectionList .sectionList__section {
      height: 20vh; }
      .sectionList .sectionList__section:last-child {
        margin-bottom: 0; } }



